.project .title {
  font-size: 2.2rem;
  font-weight: bold;
}

.project .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
}

.project .container>div {
  -ms-flex: 1 1 12rem;
      flex: 1 1 12rem;
  margin: .5rem 1rem 0 1rem;
}

.project .container .name {
  color: #FFF;
  font-weight: bold;
  padding-right: 1.6rem;
  margin-bottom: .4rem;
  position: relative;
}

.project .container .icon{
  display: inline-block;
  background-color: #FFC408;
  position: absolute;
  top: 0;
  right: 0;
  height: 1.6rem;
  line-height: 1.6rem;
  text-align: center;
  width: 1.6rem;
  border-radius: .8rem;
  margin-left: .5rem;
}

.project .container .icon a {
  color: #005CAF;
}

.project .container .name .en {
  color: #FFF;
}

.project .container .work {
  font-size: .7rem;
  text-align: left;
}

.project .more {
  margin-top: .5rem;
  color: #FFC408;
}



